import React from "react";
import { FaEdit } from "react-icons/fa";
import { useNavigate } from "react-router-dom";
import { ScaleLoader } from "react-spinners";
import { TbReplace } from "react-icons/tb";
import { CgRemoveR } from "react-icons/cg";

import useQueryParam from "../../hook/useQueryParam";
import { Button, Form } from "react-bootstrap";
import convertBase64 from "../GenericActions";
import AWSImageUpload from "../../app/features/bucketImageUpload/bucketImageUpload.service";
import { PATH } from "../../utils/path";

export default function ProfileImgSection({
  eventKey,
  setImageUrl,
  imageUrl,
  setIsEditing,
  setLoading,
  loading,
  getUserData,
}) {
  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const query = useQueryParam();
  const profileQuery = query.get("profile");

  const navigate = useNavigate();

  const handleEditClick = () => {
    const editPath =
      user?.roleId === 3
        ? `${PATH?.PATIENT_PROFILE}?profile=edit`
        : `${PATH?.PROFILE}?profile=edit`;

    navigate(editPath);
    setIsEditing(false);
  };

  const handleChangeUpload = async (e) => {
    let file = e.target.files[0];
    if (file) {
      const lastIndexOfDot = file.name.lastIndexOf(".");
      const name = file.name.slice(0, lastIndexOfDot);
      const ext = file.name.slice(lastIndexOfDot + 1, file.name.length);
      // convertBase64(file, (url) => {
      convertBase64(file).then((url) => {
        setLoading(true);
        const fileData = {
          name: name,
          base64: url,
          fileExtension: `${ext}`,
        };
        AWSImageUpload(fileData).then((response) => {
          if (response) {
            setImageUrl(response);
            setLoading(false);
          } else {
            // notification.error({
            //   message: response?.message,
            // });
          }
        });
      });
    }
  };
  return (
    <div className="tab__content-header">
      <div className="h-100 d-flex justify-content-between ">
        <div className="position-relative">
          <div className="user__outer-section">
            {loading ? (
              <div
                className="h-100 d-flex align-items-center justify-content-center"
                style={{
                  background: "#000071",
                  borderRadius: "50%",
                }}
              >
                <ScaleLoader color="#F26522" size={30} />
              </div>
            ) : (
              <img
                src={
                  imageUrl?.baseUrl ||
                  getUserData?.imageUrl ||
                  `https://ui-avatars.com/api/?name=${
                    `${encodeURIComponent(
                      getUserData?.firstName
                    )}+${encodeURIComponent(getUserData?.lastName)}` ||
                    encodeURIComponent(getUserData?.name)
                  }&background=000071&color=fff`
                }
                alt={`${
                  getUserData?.name ||
                  `${getUserData?.firstName} ${getUserData?.lastName}`
                }'s avatar`}
                srcSet=""
              />
              
            )}
          </div>
         
          {eventKey === "personalInfo" && profileQuery !== "info" && (
            <div
              className="position-relative profile__upload-btns"
              style={{ top: "1.5rem", left: "12rem" }}
            >
              <Button className="profile__change-btn border-0 px-4 h-100">
                <label htmlFor="profile-pic" className="d-none d-sm-block">
                  Change
                </label>
                <TbReplace size={22} className="d-sm-none" />
                <Form.Control
                  onChange={(e) => handleChangeUpload(e)}
                  type="file"
                  id="profile-pic"
                  name="profilePicture"
                  accept="image/*"
                />
              </Button>
              <Button
                className="profile__remove-btn border-0 px-4 ms-3 h-100"
                onClick={() => setImageUrl("")}
              >
                <CgRemoveR
                  size={22}
                  style={{ color: "#FD2121" }}
                  className="d-sm-none"
                />
                <span className="d-none d-sm-block">Remove</span>
              </Button>
            </div>
          )}
        </div>
        {eventKey === "personalInfo" && profileQuery === "info" && (
          <div className="d-flex align-items-end pb-4 pe-4 rm__position-alignment">
            <button
              type="button"
              className="btn text-decoration-none profile__edit-btn bg-white d-flex px-3 py-2"
              onClick={handleEditClick}
            >
              <FaEdit className="me-2" size="22" />
              <span>Edit</span>
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
