import React, { useState, useRef } from "react";
import { Row, Col, Form, Button } from "react-bootstrap";
import { AiOutlineEye, AiOutlineEyeInvisible } from "react-icons/ai";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { changePassword } from "../../../../app/features/auth/auth.slice";

function DoctorProfileChangePassword() {
  const { getUserData, isLoading, isSuccess, isError } = useSelector(
    (state) => state.userRole
  );
  const dispatch = useDispatch();
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const [newPassword, setNewPassword] = useState(false);
  const handleClickNewPassword = () => setNewPassword((show) => !show);

  const [confirmPassword, setConfirmPassword] = useState(false);
  const handleClickConfirmPassword = () => setConfirmPassword((show) => !show);

  const {
    register,
    watch,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const password = useRef({});
  password.current = watch("password", "");

  const moveToNext = () => {
    reset({
      oldPassword: "",
    });
  };
  function onSubmitConfirmPassword(data) {
    const finalData = {
      password: data.oldPassword,
      newPassword: data.password,
    };
    dispatch(changePassword({ finalData, moveToNext }));
  }

  return (
    <>
      <Row className="mt-1 Password-Input-Spacing"></Row>
      {isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        <Form onSubmit={handleSubmit(onSubmitConfirmPassword)}>
          <Row className="gy-4 gx-5 mt-4 d-grid">
            <Col xl={6} lg={12} md={6} xs={12}>
              <Form.Group
                className="position-relative font-bold"
                controlId="doctorOldPassword"
              >
                <Form.Label
                  className="profile__field-label"
                  style={{ color: "#1A1A1A" }}
                >
                  Old Password
                </Form.Label>
                <Form.Control
                  type={showPassword ? "text" : "password"}
                  placeholder="Password"
                  className="profile__field-label font-bold"
                  size="lg"
                  name="oldPassword"
                  {...register("oldPassword", {
                    required: true,
                  })}
                />
                {errors.oldPassword && (
                  <p className="text-danger">{errors.oldPassword.message}</p>
                )}
                <div onClick={handleClickShowPassword} className="eye-icon">
                  {showPassword ? (
                    <AiOutlineEye size={18} />
                  ) : (
                    <AiOutlineEyeInvisible size={18} />
                  )}
                </div>
              </Form.Group>
            </Col>
            <Col xl={6} lg={12} md={6} xs={12}>
              <Form.Group
                className="position-relative"
                controlId="doctorNewPassword"
              >
                <Form.Label
                  className="profile__field-label font-bold"
                  style={{ color: "#1A1A1A" }}
                >
                  New Password
                </Form.Label>
                <Form.Control
                  type={newPassword ? "text" : "password"}
                  placeholder="Password"
                  className="Field-Sizing"
                  size="lg"
                  name="password"
                  {...register("password", {
                    required: true,
                  })}
                />
                {errors.password && (
                  <p className="text-danger">{errors.password.message}</p>
                )}
                <div onClick={handleClickNewPassword} className="eye-icon">
                  {newPassword ? (
                    <AiOutlineEye size={18} />
                  ) : (
                    <AiOutlineEyeInvisible size={18} />
                  )}
                </div>
              </Form.Group>
            </Col>
            <Col xl={6} lg={12} md={6} xs={12}>
              <Form.Group
                className="position-relative"
                controlId="doctorConfirmPassword"
              >
                <Form.Label
                  className="profile__field-label"
                  style={{ color: "#1A1A1A" }}
                >
                  Confirm Password
                </Form.Label>
                <Form.Control
                  type={confirmPassword ? "text" : "password"}
                  placeholder="Password"
                  name="confirmPassword"
                  size="lg"
                  {...register("confirmPassword", {
                    validate: (value) =>
                      value === password.current ||
                      "The passwords does not match",
                  })}
                />
                {errors.confirmPassword && (
                  <p className="text-danger">
                    {errors.confirmPassword.message}
                  </p>
                )}
                <div onClick={handleClickConfirmPassword} className="eye-icon">
                  {confirmPassword ? (
                    <AiOutlineEye size={18} />
                  ) : (
                    <AiOutlineEyeInvisible size={18} />
                  )}
                </div>
              </Form.Group>
            </Col>
            <Col xl={6} lg={12} md={6} xs={12}>
              <Button
                variant="primary"
                className="Save-password-button border-0 w-100"
                type="submit"
              >
                Save Password
              </Button>
            </Col>
          </Row>
        </Form>
      ) : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null}
    </>
  );
}
export default DoctorProfileChangePassword;
