import React from "react";
import { Col, Row } from "react-bootstrap";

import ProfileEdit from "./ProfileEdit";
import { useSelector } from "react-redux";

export default function PersonalInformation({
  profileQuery,
  imageUrl,
  setProfileImage,
  setIsEditing,
  loading,
}) {
  const { getUserData, isLoading, isSuccess, isError } = useSelector(
    (state) => state.userRole
  );

  return (
    <>
    {profileQuery === "info" && (
      isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        
          <Row className="gy-4 gx-2 mt-4">
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Name</p>
              <h5 className="profile__field-value">
                {getUserData?.firstName
                  ? `${getUserData?.firstName} ${getUserData?.lastName}`
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Gender</p>
              <h5 className="profile__field-value">
                {getUserData?.genderName ? getUserData?.genderName : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Phone</p>
              <h5 className="profile__field-value">
                {getUserData?.phoneNumber ? getUserData?.phoneNumber : "N/A"}
              </h5>
            </Col>

            <Col xl={4} sm={6}>
              <p className="profile__field-label">Medical Council Number</p>
              <h5 className="profile__field-value">
                {getUserData?.mcrn ? getUserData?.mcrn : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Email</p>
              <h5 className="profile__field-value">
                {getUserData?.email ? getUserData?.email : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Address</p>
              <h5 className="profile__field-value">
                {getUserData?.currentAddress
                  ? getUserData?.currentAddress
                  : "N/A"}
              </h5>
            </Col>
            <Col xl={4} sm={6}>
              <p className="profile__field-label">Signature</p>
              {getUserData?.doctorSignature ? (
                <span
                  style={{
                    display: "block",
                    border: "1px solid black",
                    width: "150px",
                    height: "auto",
                  }}
                  className="mt-4"
                >
                  <img
                    src={getUserData?.doctorSignature}
                    alt="signature"
                    width="100%"
                    height="100px"
                  />
                </span>
              ) : (
                <p
                  className="profile__field-value"
                  // style={{ marginTop: "30px" }}
                >
                  N/A
                </p>
              )}
            </Col>
          </Row>
        )
       : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null
    )}
      {(profileQuery === "edit" || profileQuery === "signature") && (
        <ProfileEdit
          profileQuery={profileQuery}
          imageUrl={imageUrl}
          setProfileImage={setProfileImage}
          setIsEditing={setIsEditing}
          loading={loading}
        />
      )}
    </>
  );
}
