import React, { useState } from "react";
import { Col, Container, Nav, Row, Tab } from "react-bootstrap";
import ProfileImgSection from "../../ProfileImgSection";
import { IoIosArrowForward } from "react-icons/io";
import { useDispatch } from "react-redux";
import { requestPermission } from "../../../firebase";
import { logoutUser } from "../../../app/features/auth/auth.slice";
import { deletePatientAccount } from "../../../app/features/patient/patient.slice";
import { ConfirmationModal } from "../../Modal/ConfirmationModal";

export default function PersonalProfileLayout({
  navEventLinks,
  setImageUrl,
  imageUrl,
  setIsEditing,
  loading,
  setLoading,
  getUserData,
  onNavClick,
  deleteAccount,
  children,
}) {
  const [open, setOpen] = useState(false);
  const user = JSON.parse(localStorage.getItem("family_doc_app")) || {};

  const dispatch = useDispatch();

  const logoutAccount = () => {
    requestPermission().then((currentToken) => {
      const finalData = {
        userId: user?.userId,
        deviceId: currentToken || "",
      };
      dispatch(logoutUser(finalData));
    });
  };

  function deleteAccountFn() {
    const finalData = {
      id: user?.userId,
    };
    dispatch(deletePatientAccount({ finalData, logoutAccount }));
  }
  const handleNavClick = (key) => {
    if (deleteAccount) {
      setOpen(true);
    } else {
      onNavClick(key);
    }
  };

  const navItems = navEventLinks?.filter((item) => item?.key !== "deleteAcc");
  const deleteNavItem = navEventLinks?.find(
    (item) => item?.key === "deleteAcc"
  );

  return (
    <div className="personal__profile-layout">
      <Container fluid>
        <Tab.Container
          id="left-tabs-example"
          defaultActiveKey={navEventLinks[0]?.key}
        >
          <Row className="gy-2 gx-4 h-100">
            <Col lg={3}>
              <Nav variant="pills" className="flex-column p-3 tabs_panel">
                {navItems?.map((item) => (
                  <Nav.Item key={item?.key}>
                    <Nav.Link
                      eventKey={item?.key}
                      onClick={() => onNavClick(item?.key)}
                      className="cursor-pointer"
                    >
                      <span className="d-flex justify-content-between align-items-center">
                        {item?.name}
                        <IoIosArrowForward className="float-right Arrow-ForwardIcon" />
                      </span>
                    </Nav.Link>
                  </Nav.Item>
                ))}
                {deleteNavItem && (
                  <Nav.Item>
                    <Nav.Link
                      onClick={() => handleNavClick(deleteNavItem?.key)}
                    >
                      <span
                        className="d-flex justify-content-between align-items-center"
                        style={{
                          color: deleteAccount ? "#E31E27" : "inherit",
                        }}
                      >
                        {deleteNavItem?.name}
                      </span>
                    </Nav.Link>
                  </Nav.Item>
                )}
              </Nav>
            </Col>
            <Col lg={9}>
              <Tab.Content>
                {React.Children.map(children, (child) => (
                  <Tab.Pane eventKey={child.props.eventKey}>
                    <ProfileImgSection
                      eventKey={child.props.eventKey}
                      setImageUrl={setImageUrl}
                      imageUrl={imageUrl}
                      setIsEditing={setIsEditing}
                      loading={loading}
                      setLoading={setLoading}
                      getUserData={getUserData}
                    />
                    {child}
                  </Tab.Pane>
                ))}
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
      </Container>
      <ConfirmationModal
        show={open}
        onHide={() => setOpen(false)}
        heading={"Delete Account"}
        title={"your account"}
        removeFunc={deleteAccountFn}
      />
    </div>
  );
}
