import React, { useRef, useState } from "react";
import { Button, Form } from "react-bootstrap";
import SignaturePad from "react-signature-canvas";
import IMAGES from "../../../../assets/images";
import { FaPlus } from "react-icons/fa";
import convertBase64 from "../../../../components/GenericActions";
import AWSImageUpload from "../../../../app/features/bucketImageUpload/bucketImageUpload.service";
import { GrDocumentCloud } from "react-icons/gr";
import { AiOutlineClose } from "react-icons/ai";
import { uploadDoctorSignature } from "../../../../app/features/doctorSchedule/doctorSchedule.slice";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";
import { getUser } from "../../../../app/features/userManagement/user.slice";
import { PATH } from "../../../../utils/path";
import { useNavigate } from "react-router-dom";

export default function DoctorSignature() {
  const { handleSubmit } = useForm();

  const [uploadPicture, setUploadPicture] = useState(true);
  const [imageURL, setImageURL] = useState(null);
  const [uploadImageURL, setUploadImageURL] = useState(null);
  const [selectedFiles, setSelectedFiles] = useState();
  const [loading, setLoading] = useState();
  const sigCanvas = useRef({});

  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const { getUserData } = useSelector((state) => state?.userRole || "");
  const clear = () => setImageURL(sigCanvas.current.clear());

  const handleSave = () => {
    const signature = sigCanvas.current
      .getTrimmedCanvas()
      .toDataURL("image/png");
    signature.substring("data:image/".length, signature.indexOf(";base64"));
    const type = signature.split(";")[0].split("/")[1];
    setLoading(true);
    const fileData = {
      name: "signatureImage",
      base64: signature,
      fileExtension: `${type}`,
    };
    AWSImageUpload(fileData).then((response) => {
      if (response) {
        setImageURL(response);
        setLoading(false);
      } else {
      }
    });
  };
const navigate = useNavigate();

  const handleCheckboxChange = (isChecked) => {
    if (!isChecked) {
      setUploadPicture(!uploadPicture);
    }
  };

  const handleChangeUpload = async (e) => {
    let file = e.target.files[0];
    setSelectedFiles(file);
    if (file) {
      const lastIndexOfDot = file.name.lastIndexOf(".");
      const name = file.name.slice(0, lastIndexOfDot);
      const ext = file.name.slice(lastIndexOfDot + 1, file.name.length);

      convertBase64(file).then((url) => {
        // setLoading(true);

        const fileData = {
          name: name,
          base64: url,
          fileExtension: `${ext}`,
        };
        AWSImageUpload(fileData).then((response) => {
          if (response) {
            setUploadImageURL(response);
            // setLoading(false);
          } else {
            // Handle the error
          }
        });
      });
    }
  };

  const handleRemoveFile = () => {
    setSelectedFiles(null);
    document.getElementById("signature-upload").value = null;
  };

  const moveToNext = (response) => {
    navigate(`${PATH.PROFILE}?profile=info`)
    const finalData = {
      userId: user?.userId,
    };
      const updatedData = updateSignature(finalData, response);
      localStorage.setItem('family_doc_app', JSON.stringify(updatedData));

    dispatch(getUser(finalData));
  };


const updateSignature = (data, response) => {
  const storedData = JSON.parse(localStorage.getItem('family_doc_app')) || {};

  storedData.doctorSignature = response?.data?.doctorSignature ? true : false;
  const updatedData = { ...storedData, doctorSignature: true };

  return updatedData;

};

  
  function onSubmit() {
    const signatureURL = {
      doctorId: user?.userId,
      doctorSignature: imageURL ? imageURL?.keyName : uploadImageURL?.keyName,
    };
    dispatch(uploadDoctorSignature({ signatureURL, moveToNext }));
  }

  return (
    <div className="px-5 py-4">
      <h5 className="fw-bold">Signature</h5>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Form.Check
          type="checkbox"
          id="upload-picture"
          label="Upload Picture"
          onChange={() => handleCheckboxChange(uploadPicture)}
          checked={uploadPicture}
          disabled={getUserData?.doctorSignature}
        />
        <Form.Check
          type="checkbox"
          id="signature-pad"
          label="Signature Pad"
          onChange={() => handleCheckboxChange(!uploadPicture)}
          checked={!uploadPicture}
          disabled={getUserData?.doctorSignature}
        />
        <div className="border py-4 rounded upload_pic">
          {uploadPicture ? (
            <>
              <label
                htmlFor="signature-upload"
                className="text-center w-100 pt-1"
              >
                <img
                  className="mb-2 upload-icon color-dk-blue"
                  src={IMAGES.UPLOAD_ICON}
                  alt="upload file"
                />
                <p className="upload-text mb-1">Upload a file</p>
                <p className="upload-text_small mb-0">
                  PNG, JPG, JPEG upto 2MB
                </p>
              </label>
              <input
                size="small"
                type="file"
                id="signature-upload"
                name="signature-upload"
                accept="image/png, image/jpeg"
                onChange={(e) => {
                  handleChangeUpload(e);
                }}
              />
              <div className="my-3">
                {selectedFiles?.name && (
                  <div className="d-flex align-items-center mx-3">
                    <GrDocumentCloud size={30} style={{ color: "#745DED" }} />
                    <h6 className="file-name mb-0 ms-2">
                      {selectedFiles?.name}
                    </h6>
                    <span className="mx-3">
                      <AiOutlineClose size={18} onClick={handleRemoveFile} />
                    </span>
                  </div>
                )}
              </div>
            </>
          ) : (
            <div className="w-100">
              <div className="d-flex justify-content-center">
                <SignaturePad
                  ref={sigCanvas}
                  canvasProps={{
                    className: "signatureCanvas rounded border me-3",
                  }}
                />
                {imageURL ? (
                  <div className="d-flex align-items-end">
                    <img
                      src={imageURL?.baseUrl}
                      alt="my signature"
                      style={{
                        display: "block",
                        border: "1px solid black",
                        width: "150px",
                        height: "100px",
                      }}
                    />
                  </div>
                ) : null}
              </div>
              <div className="mt-2 d-flex justify-content-center">
                <Button
                  onClick={handleSave}
                  // type="button"
                  style={{ background: "#000071" }}
                  className="me-2"
                >
                  <FaPlus /> Add Signature
                </Button>
                <Button
                  onClick={clear}
                  type="button"
                  style={{ background: "#000071" }}
                >
                  Clear
                </Button>
              </div>
            </div>
          )}
        </div>
        <div className="w-100 d-flex justify-content-end mt-3">
          <Button
            style={{ background: "#000071" }}
            type="submit"
            disabled={!imageURL && !selectedFiles}
          >
            Save
          </Button>
        </div>
      </Form>
    </div>
  );
}