import React, { useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import { FieldError } from "../../../../../components/ErrorMessage/ErrorMessage";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import DoctorSignature from "../../DoctorSignature";
import { useNavigate } from "react-router-dom";
import { updatedProfile } from "../../../../../app/features/auth/auth.slice";
import {
  getUser,
  updateUser,
} from "../../../../../app/features/userManagement/user.slice";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { PATH } from "../../../../../utils/path";

export default function ProfileEdit({
  profileQuery,
  imageUrl,
  setProfileImage,
  setIsEditing,
  loading,
}) {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const user = JSON.parse(localStorage.getItem("family_doc_app")) || {};
  const { getUserData, isLoading, isSuccess, isError } = useSelector(
    (state) => state.userRole
  );
  const [value, setValue] = useState(getUserData?.phoneNumber);

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const onCreateSuccess = (data) => {
    setIsEditing(true);
    const finalData = {
      userId: user?.userId,
    };
    if (user?.userId && user[user.userId]) {
      user[user.userId].imageUrl = data?.imageUrl;
      localStorage.setItem("auth", JSON.stringify(user));
    }
    dispatch(getUser(finalData));
    navigate(`${PATH.PROFILE}?profile=info`);
    setProfileImage({
      imageUrl: imageUrl,
      userName: `${data?.firstName} ${data?.lastName}`,
    });
  };

  function onSubmit(data) {
    const finalData = {
      userId: user?.userId,
      roleId: user?.roleId,
      firstName: data.firstName,
      lastName: data.lastName,
      genderId: +data.genderName,
      speciality: data.speciality,
      email: data.email,
      phoneNumber: value
        ? value
        : getUserData?.phoneNumber
        ? getUserData?.phoneNumber
        : "",
      mcrn: data.mcrn,
      currentAddress: data.currentAddress,
      image: imageUrl?.keyName
        ? imageUrl?.keyName
        : getUserData?.image
        ? getUserData?.image
        : "",
      doctorSignatureKey: getUserData?.doctorSignatureKey,
    };
    const callBackFunc = () => {
      dispatch(updatedProfile());
    };
    dispatch(updateUser({ finalData, onCreateSuccess, callBackFunc }));
  }
  return (
    <div className="profile__edit-section">
      {isLoading ? (
        <p
          className="my-5 text-center fst-italic"
          style={{
            color: "#999999",
          }}
        >
          Loading...
        </p>
      ) : isSuccess ? (
        <>
          {profileQuery === "edit" && (
            <Form onSubmit={handleSubmit(onSubmit)}>
              <Row className="gy-4 gx-5 mt-4">
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    First Name
                  </Form.Label>
                  <Form.Control
                    name="firstName"
                    type="text"
                    placeholder="John Smith"
                    {...register("firstName")}
                    defaultValue={getUserData?.firstName}
                  />
                  {errors.firstName && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Last Name
                  </Form.Label>
                  <Form.Control
                    name="lastName"
                    type="text"
                    placeholder="John Smith"
                    {...register("lastName")}
                    defaultValue={getUserData?.lastName}
                  />
                  {errors.lastName && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Gender
                  </Form.Label>
                  <Form.Select
                    name="genderName"
                    className="Select-Dropdown-Sizing"
                    {...register("genderName")}
                    defaultValue={getUserData?.genderName}
                  >
                    <option value={101}>Male</option>
                    <option value={102}>Female</option>
                    <option value={103}>Other</option>
                  </Form.Select>
                  {errors.genderName && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{ color: "#1A1A1A" }}
                  >
                    Email
                  </Form.Label>
                  <Form.Control
                    name="email"
                    type="email"
                    placeholder="patient@gmail.com"
                    {...register("email", {
                      pattern: /^[^@ ]+@[^@ ]+\.[^@ .]{2,}$/,
                    })}
                    defaultValue={getUserData?.email}
                  />
                  {errors.email && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                    Phone Number
                  </Form.Label>
                  <PhoneInput
                    country="ie"
                    className="Phone-Number-FieldWidth border rounded"
                    value={getUserData?.phoneNumber}
                    onChange={(value) => setValue(`+${value}`)}
                  />
                  {errors.phoneNumber && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col xl={4} sm={6}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                    Medical Council Number
                  </Form.Label>
                  <Form.Control
                    name="mcrn"
                    type="text"
                    placeholder="788 988 98 888"
                    {...register("mcrn")}
                    defaultValue={getUserData?.mcrn}
                  />
                  {errors.mcrn && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>

                <Col lg={8}>
                  <Form.Label
                    className="profile__field-label"
                    style={{
                      color: "#1A1A1A",
                    }}
                  >
                    Address
                  </Form.Label>
                  <Form.Control
                    name="currentAddress"
                    type="text"
                    placeholder="4517 Washington Ave. Manchester, Kentucky 39495"
                    className="Address-Input-Height w-100"
                    {...register("currentAddress")}
                    defaultValue={getUserData?.currentAddress}
                  />
                  {errors.currentAddress && (
                    <FieldError message={"This Field is Required"} />
                  )}
                </Col>
                <Col lg={4} className="d-flex align-items-end">
                  <span
                    className={`d-flex ${
                      getUserData?.doctorSignature ? "color-99" : ""
                    }`}
                  >
                    <p
                      className={`mb-0 text-decoration-underline text-cursor-pointer ${
                        getUserData?.doctorSignature ? "disabled" : ""
                      }`}
                      onClick={() => {
                        if (!getUserData?.doctorSignature) {
                          navigate(`${PATH.PROFILE}?profile=signature`);
                        }
                      }}
                    >
                      Upload Signature
                    </p>

                    <span style={{ color: "#FF3A3A" }}>*</span>
                  </span>
                </Col>
              </Row>
              <div className="mt-4 d-flex flex-wrap justify-content-end">
                <Button
                  onClick={() => navigate(`${PATH.PROFILE}?profile=info`)}
                  className="Save-Changes-Btn border-0 text-black"
                  style={{
                    background: "#eae5e5",
                    borderColor: "#eae5e5",
                  }}
                >
                  Cancel
                </Button>
                <Button
                  className="Save-Changes-Btn"
                  style={{ background: "#000071" }}
                  type="submit"
                  // onClick={() =>
                  //   navigate(
                  //     `${PATH.PROFILE}?profile=info`
                  //   )
                  // }
                  disabled={loading}
                >
                  Save Changes
                </Button>
              </div>
            </Form>
          )}
          {profileQuery === "signature" && <DoctorSignature />}
        </>
      ) : isError ? (
        <p className="my-5 text-center text-danger fst-italic">Network Error</p>
      ) : null}
    </div>
  );
}
